.fixed {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.wrapper {
  width: 100%;
  height: 100%;
  position: relative;
}

.hoverable {
  transition: box-shadow 0.25s;
  &:hover {
    box-shadow: -3.125px 3.125px 6.25px 1.5px rgba(0, 0, 0, 0.15);
  }
}
